@font-face {
	font-family: "Skaryna 2017 Title";
	font-style: normal;
	font-weight: 500;
	/* Браузер сначала попробует найти шрифт локально */
	src: local("Skaryna 2017 Title"),
		/* Если не получилось, загрузит woff2 */
		url("../../fonts/skaryna-title-2017-CC-v20200624-1052.ttf");
}

.header {
	grid-area: h;
	background-color: #F5F5F5;
}

.wrapper {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 1.5rem;
}

.text,
.login,
.logo {
	color: var(--red-main);
	color: #293138;
	text-align: center;
	margin: auto 0 auto 60px;
	display: inline-block;
	padding-top: 1rem;
}

.text a:last-child {
	color: #293138;
	text-align: center;
	margin: 0 auto;
	display: inline-block;
}

.logo {
	margin-left: 0;
	display: flex;
	align-items: center;
	color: var(--red-main);
}

.image {
	height: 127px;
	background-color: #F5F5F5;
}

.near {
	font-size: 2.5rem;
	font-weight: 600;
	font-family: "Skaryna 2017 Title";
}

.social {
	font-style: italic;
	margin-left: 1rem;
	font-size: 0.8rem;
	display: flex;
	align-self: flex-end;
	margin-bottom: 5px;
}

.sign {
	font-size: 0.8rem;
	margin-bottom: 0.5rem;
}

.name a {
	font-size: 1.1rem;
	font-weight: 500;
	display: flex;
	align-items: center;
	color: var(--red-main);
}

.avatar {
	border-radius: 50%;
	width: 1.5rem;
	height: 1.5rem;
}

.rightBlock {
	display: flex;
	align-items: center;
}

.mute {
	padding-top: 1rem;
	margin-left: 2rem;
}

.mute svg {
	cursor: pointer;
}
